.container {
    max-width: 1140px;
    margin: auto;
}
.heading{

    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #26272b;
    box-shadow: 0px 3px 12px #18191b;
    border-radius: 8px;
    margin: 1rem 0rem;
    padding: .7rem 1rem;
    font-weight: 700;
}

.coin-name{
    margin-left: -4rem;
}

.coin-row{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #26272b;
    box-shadow: 0px 3px 12px #18191b;
    border-radius: 8px;
    margin: 2rem 0rem;
    padding: .7rem 1rem; 
}
.coin-row:hover{
    transform: scale(1.05);
    transition: .1.2s ease-in-out;
    cursor: pointer;
}
img{
    height: 40px;
    margin-right: 8px;
    width: auto;
}
.red{
    color: red;
}
.green{
    color: green;
}

@media screen and (max-width :720px) {
 .hide-mobile{
    display: none;
 }    
}